.other-goods {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  padding-top: 119px;
  padding-bottom: 30px;
  &__our-things {
    color: #9297a3;
    font-family: Georgia, sans-serif;
    font-size: 11px;
    font-weight: 400;
    line-height: 16px;
  }
  &__banner {
    color: #333743;
    font-family: "PT Sans - Narrow Bold", sans-serif;
    font-size: 16px;
    font-weight: 700;
    line-height: 16px;
    text-transform: uppercase;
  }
  &__item-container {
    display: grid;
    grid-template-columns: 0.1fr 1fr 1fr 1fr 1fr 1fr 0.1fr; /* кол-во колонок (тут 7шт.) 1fr- единица пропроционально занимаемого места*/
    //grid-column-gap: 19px;         //отступ между столбцами
    grid-row-gap: 19px; //отступ между строками
    // grid-template-rows: 1fr 3fr;   //высота строк: 1я строка -1fr, 2я - 3fr
  }

  &__item {
    width: 226px;
    text-align: center;
    margin: 0 auto;
  }
  /*&__empty {
    background-color: red;
  }*/

  &__image-container {
    background-color: #f5f5f5;
    width: 226px;
    height: 225px;
    margin: 0 auto;
  }

  &__product-description {
    display: block;
    h4 {
      float: left;
      color: #333743;
      font-family: "PT Sans - Narrow Bold", sans-serif;
      font-size: 16px;
      font-weight: 700;
      text-transform: uppercase;
      text-align: left;
      margin-top: 18px;
      margin-bottom: 0;
    }
    h5 {
      float: right;
      margin-top: 18px;
      color: #9297a3;
      font-family: Georgia, sans-serif;
      font-size: 13px;
      font-weight: 400;
      padding-right: 9px;
      margin-bottom: 0;
    }
    p {
      clear: both;
      margin-top: 5px;
      text-align: left;
      color: #9297a3;
      font-family: Georgia, sans-serif;
      font-size: 11px;
      font-weight: 400;
    }
  }

  /*.item__bag {
    background-color: greenyellow;
  }*/

  @media all and (max-width: 1640px) {
    &__item-container {
      grid-template-columns: 0.1fr 1fr 1fr 1fr 1fr 1fr 0.1fr;
    }
  }

  @media all and (max-width: 1440px) {
    &__item-container {
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    }
    &__empty {
      display: none;
    }
  }

  @media all and (max-width: 1350px) {

    &__item-container {
      grid-template-columns: 1fr 1fr 1fr;
    }
    &__item {
      width: 80%;
    }
  }

  @media all and (max-width: 900px) {
    padding-top: 60px;
    &__item-container {
      grid-template-columns: 1fr 1fr;
    }
  }

  @media all and (max-width: 630px) {
    &__item-container {
      grid-template-columns: 1fr;
    }
  }

  @media all and (max-width: 502px) {
    &__item-container {
      grid-template-columns: 1fr;
    }
  }
}